.fade-enter {
    opacity: 0;
    transform: scale(1.1);
    transform-origin: center center;
}

.fade-enter-active {
    opacity: 1;
    transform: scale(1);
    transition: opacity 300ms, transform 300ms;
}

.fade-exit {
    opacity: 1 !important;
    transform: scale(1) !important;
    transform-origin: center center;
}

.fade-exit-active {
    opacity: 0 !important;
    transform: scale(0.9) !important;
    transition: opacity 300ms, transform 300ms;
}