@import "styles/responsive.scss";

.Director {
    position: relative;
    width: 90%;
    padding: 20px 30px;
    text-align: center;
    margin-top: 20px;
    border-radius: 10px;
    color: #6F6F6F;
    background-color: #F0F0F0;
    border: 0px;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    @include mobile {
        width: calc(100% - 40px) !important;
        padding: 20px 20px !important;


        h5 {
            font-size: 0.8rem !important;
        }
    }

    .Arrow {
        transition: 0.3s transform;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
            height: 20px;
        }
    }

    .ArrowEnable {
        transform: rotate(180deg);        
    }

    h5 {
        font-size: 1.3vw;
    }      
}

.DirectorModal {
    border-radius: 10px;
    width: calc(100% + 10px);
    background-color: #F0F0F0;
    height: 0px;
    position: absolute;
    z-index: 100;
    bottom: 100px;
    transition: height 0.4s, opacity 0.4s;
    overflow: hidden;
    text-align: center;
    h2 {
        padding: 10px 0px;
    }
    color: #6F6F6F;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    will-change: height;

    @include mobile {
        width: 100%;
        bottom: 70px;
        h2 {
            font-size: 1rem;
        }
    }
}

.DirectorModalEnable {
    height: 250px;
    opacity: 1;

    @include mobile {
        height: 200px;
    }
}