@import "styles/responsive.scss";

.ModalEnable {
    overflow: hidden;
}

.ModalLoad {
    opacity: 0;

    .Modal {
        transform: translateY(20px);
        opacity: 0;
    }
}

.PrizeModal::-webkit-scrollbar {
    display: none;
}

.PrizeModal {
    border-radius: 20px;
    width: 100%;
    height: 100%;
    overflow-y: scroll;

    @include mobile {
        h1 {
            font-size: 20px;
        }

        .Content {
            font-size: 15px !important;
        }
    }

    .Content {
        border-radius: 20px;
        background-color: rgba(200, 200, 200, 0.3);
        overflow: hidden;
        transition: 0.3s;
        max-height: 0px;
        padding: 0px 30px;
        opacity: 0;
        will-change: max-height, opacity;
    }

    input[type="checkbox"] {
        display: none;
    }

    input[type="checkbox"]:checked + label {

        img {
            transform: rotate(180deg);
        }
    }

    input[type="checkbox"]:checked + label + .Content {
        max-height: 100px;
        padding: 20px 30px;
        opacity: 1;
    }

    .Prize {
        display: block;
        gap: 20px;
        padding: 13px 25px;
        margin: 15px 0px;
        cursor: pointer;
        border-radius: 20px;
        background-color: rgba(200, 200, 200, 0.5);
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;

        @include mobile {
            margin: 10px 0px !important;
            span {
                font-size: 12px !important;
            }
        }

        img {
            width: 30px;
            height: 30px;
            transition: 0.3s;
        }

        .award {
            color: rgb(130, 130, 130);
        }
    }
}

.ModalFrame {
    @include mobile {
        display: flex;
        flex-direction: column-reverse;
    }

    transition: 0.3s;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    background-color: rgba(50, 50, 50, 0.5);
    z-index: 100;
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: center;
    align-items: flex-end;
    will-change: opacity;
    backdrop-filter: blur(3px);

    button {
        margin-right: 20%;
        width: 100px;
        height: 50px;
        font-size: 17px;
        background-color: #ed6474;
        border: 0px;
        color: white;
        cursor: pointer;
        border-radius: 20px;

    }

    .Modal {
        transition: 0.3s;
        margin: 0 auto;
        padding: 40px 50px;
        width: calc(60% - 100px);
        height: calc(80% - 80px);
        background-color: rgb(240, 240, 240);
        border-radius: 30px;


        @include mobile {
            width: calc(100% - 100px);
            height: calc(100% - 160px);
            padding: 20px !important;
        }
    }
}




.PortfolioModal {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 30% 1fr;
    gap: 40px;

    @include mobile {
        h1 {
            font-size: 30px !important;
        }

        .TextBox {
            p {
                font-size: 15px !important;
            }
        }
    }

    h1 {
        font-size: 2.5vw;
        width: 80%;
        word-break: keep-all;
    }

    .TextBox {
        height: 100%;
        overflow-y: scroll;

        p {
            font-size: 18px;
            padding: 1px 1px;
        }

        .Icon {
            width: 50px;
            height: 50px;
        }


        .Label {
            display: inline-block;
            border-bottom: 3px solid #26CE8A;
            font-size: 1.3vw;
            margin-top: 30px;
            margin-bottom: 10px;

        }
    }

    .ImageBox {
        margin-top: -20px;
        position: relative;
        height: calc(100% + 20px);
        overflow-y: scroll;

        img {
            width: 100%;
            border-radius: 20px;
            margin-bottom: 20px;
        }

        .TopShadow,
        .BottomShadow {
            background: linear-gradient(to bottom, rgba(240, 240, 240, 1), rgba(240, 240, 240, 0.7), rgba(240, 240, 240, 0));
            width: 100%;
            height: 50px;
            position: sticky;
        }

        .TopShadow {
            top: 0px;
        }

        .BottomShadow {
            transform: rotate(180deg);
            bottom: 0px;
        }
    }

    .ImageBox::-webkit-scrollbar {
        display: none;
    }
}