@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);
@import "styles/responsive.scss";

* {
  margin: 0px;
  padding: 0px;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  font-family: 'Spoqa Han Sans Neo', 'sans-serif';
}

body {
  overflow: hidden;
  color: rgb(20, 20, 20);
}

@for $i from 1 to 9 {
  .w#{$i} {
    font-weight: ($i * 100);
  }
}

hr {
  border: 0px;
  height: 0.5px;
  width: 80%;
  margin: 0 auto;
}



section {
  width: calc(100vw - 15%);
  margin: 0 auto;
  height: 95vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 40px;

  .Stack {
    @include mobile {
      width:  70%;
      flex-direction: column;
      height: auto !important;
      gap: 10px;

      .Block {
        padding: 13px !important;
      }
    }
    display: flex;
    flex-direction: row;
    gap: 2vw;
    height: 25vh;

    .Block {
      background-color: rgb(245, 245, 245);
      max-width: 400px;
      padding: 20px;
      border-radius: 20px;
    }
  }
}

.popEffect {
  transition: 0.3s;
}

.popEffect:hover {
  transform: translateY(-5px);
  z-index: 0;
  @include mobile {
    transform: translateY(-3px);
  }
}

.green {
  color: #26CE8A;

  * {
    color: #26CE8A;

  }
}

.blue {
  color: #1876FB;
}


*::-webkit-scrollbar {
  width: 6px;
}

*::-webkit-scrollbar-thumb {
  height: 10%;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 10px;
}

*::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0);
}