@import "styles/responsive.scss";

.AboutPage {
    width: 100vw;
    height: 100vh;
    left: 0px;
    top: 0px;
    overflow-y: scroll;
    will-change: transform;

    section {
        height: 60vh;

        @include mobile {
            height: auto !important;
        }
    }

    .About1 {
        margin-top: 150px;
        @include mobile {
            display: flex;
            flex-direction: column-reverse;
            margin-top: 100px;
            gap: 20px !important;
            p {
                white-space: normal;
            }

            .SchoolImg {
                overflow: hidden;
                height: 150px !important;
                object-fit: contain;
                border-radius: 20px;
            }

            h1 {
                font-size: 30px !important;
            }

            p {
                font-size: 10px;
            }

            .Web {
                flex-direction: row !important;
                gap: 20px !important;
                img {
                    width: 30px;
                }
            }
        }

        .SchoolImg {
            height: 100%;
            object-fit: cover;
        }

        p {
            color: rgb(100, 100, 100);
        }

        .Web {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            gap: 10px;

            h5 {
                text-align: center;
                color: #29E89B;
            }
        }

        h1 {
            font-size: 50px;
        }
    }


    .About2 {
        margin-top: 40px;
        @include mobile {
            display: flex;
            flex-direction: column-reverse;
            align-items: flex-end;
            margin-top: 20px;
            position: relative;
            gap: 20px;
            h1 {
                font-size: 30px !important;
            }


            .Stack {
                right: 0px;
                p {
                    font-size: 10px;
                }

                .FinderLogo {
                    height: 100px !important;
                }   
            }
        }

        .Stack {
            justify-content: end;
            
            p {
                color: rgb(100, 100, 100);
            }

            .Finder {
                background-color: rgba(255, 255, 255, 0.1);
                display: flex;
                align-items: center;
                justify-content: center;

                .FinderLogo {
                    height: 100%;
                    padding: 0px 40px;
                }

            }
        }

        h1 {
            font-size: 50px;
            text-align: right;
        }
    }


    .About3 {

        @include mobile {
            display: flex;
            flex-direction: column-reverse;
            gap: 20px;
            margin-top: 20px;
            h1 {
                font-size: 30px !important;
            }


            .Skill {
                width: 80% !important;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-evenly;
                span {
                    bottom: auto !important;
                    left: auto !important;
                    position: relative !important;
                }
            }
        }
        margin-bottom: 15vh;
        .Skill {
            background-color: rgb(245, 245, 245);
            padding: 20px;
            width: 140px;
            border-radius: 20px;
            position: relative;


            span {
                position: absolute;
                bottom: 20px;
                left: 20px;
                color: rgb(10, 10, 10);

                h6 {
                    color: rgb(100, 100, 100);
                }
            }

            img {
                height: 50px;
            }
        }

        h1 {
            font-size: 50px;
        }
    }
}
