@import "styles/responsive.scss";

.Navbar {
    width: 100%;
    height: 130px;
    position: fixed;
    top: 0px;
    background: linear-gradient(to bottom, white, rgba(255,255,255,0.8), rgba(255,255,255,0));

    .InnnerNavbar {
        width: 85%;
        height: 100%;
        margin: 0 auto;
        display: flex;
        align-items: center;
        position: relative;

        @include mobile {
            width: 90%;
            justify-content: center;
            height: 100px;
            .Logo {
                height: 35px !important;
            }
            .Shortcut {
                display: none;
            }
        }

        .Logo {
            height: 50px;
            cursor: pointer;
        }

        .Shortcut {
            position: absolute;
            right: 0px;
        }


        .DirectorFrame {
            width: 240px;
            position: relative;
            margin-left: 50px;

            .DirectorModal {
                top: 70px;
                width: calc(100% + 35px);
                h2 {
                    font-size: 15px;
                }

                @include mobile {
                    width: 100% !important;
                }
            }

            .DirectorModalEnable {
                height: 200px;

                @include mobile {
                    height: 180px;
                }
            }

            .Director {
                margin-top: 0px;
                height: 15px;
                h5 {
                    font-size: 15px;
                }

                @include mobile {
                    height: 8px;
                }
            }

        }
    }
}