.darkmode {
    width: 100%;
    height: 100%;
    background-color: #1A1B1E;
    color: #fff;

    .Shortcut {
        filter: brightness(4);
    }


    .Director {
        background-color: #2B2C31;
        color: white;
    }

    .DirectorModal {
        background-color: #2B2C31;
        color: white;
    }


    .Navbar {
        background: linear-gradient(to bottom, rgb(26, 27, 30), rgba(26, 27, 30, 0.8), rgba(26, 27, 30, 0));
    }

    .AboutPage {
        .Block {
            background-color: #1F1F22;
        }

        .finder {
            background-color: #E8E8E8;
        }

        .Skill {
            background-color: #1F1F22;

            h2 {
                color: white;
            }
        }

    }

    .Skillpage {
        .ImgFrame {
            background-color: #1F1F22 !important;

        }

        .ContentFrame {
            background-color: #303134 !important;
        }
    }


    .Gallery {
        .Line {
            img {
                box-shadow: none;
                filter: brightness(0.6);
            }
        }
    }

    .PortfolioPage {
        .Prize {
            background-color: rgb(40, 40, 40);
            border: none;
            box-shadow: none;
        }

        .Content {
            .TextBox {
                background-color: rgb(35, 35, 35);

                label {
                    background-color: rgba($color: #FFFFFF, $alpha: 0.1);
                    color: rgb(160, 160, 160);
                }
            }

        }

        .Item {
            border: none;
            box-shadow: none;

            .background {
                filter: brightness(0.5);
            }
        }
    }


    .ModalFrame {
        background-color: rgba(30, 30, 30, 0.6);
        .Modal {
            background-color: rgb(26, 27, 30);
        }

        .TopShadow, .BottomShadow {
            background: linear-gradient(to bottom, rgba(26, 27, 30, 1), rgba(26, 27, 30, 0.7), rgba(26, 27, 30, 0));
        }
    }

    .ContactPage {
        .Block {
            background-color: rgb(31, 31, 35);
        }
    }


    .PrizeModal {
        .Content {
            background-color: rgba(50, 50, 50, 0.3);
        }

        .Prize {
            background-color: rgba(50, 50, 50, 0.5);
        }
    }

}