@import "styles/responsive.scss";

.PortfolioPage {

    @include mobile {

        .Prize {
            height: 250px !important;
        }
        .TitleSection {
            flex-direction: column !important;
            align-items: baseline !important;
            height: auto !important;
            margin-top: 100px !important;
            gap: 10px;

            h1 {
                font-size: 40px !important;
            }
            .Effect {
                width: 100% !important;
                margin-top: 0px;
                font-weight: 10px !important;
                position: relative;
            }
        }

        .Content {
            margin-top: 40px;
            grid-template-columns: 1fr !important;
        }
    }

    width: 100vw;
    height: 100vh;
    overflow-y: scroll;
    will-change: transform;

    .Content {
        display: grid;
        height: auto;
        grid-template-columns: repeat(3, 1fr);
        margin-bottom: 200px;
    }

    .Prize {
        transition: 0.3s;
        cursor: pointer;
        width: 100%;
        height: 100%;       
        box-shadow: 0px 0px 15px -8px #ddd;
        border: 0.01px solid rgb(240, 240, 240);
        background-color: rgb(245, 245, 245);
        border-radius: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        position: relative;
        img {
            margin-bottom: 20px;
            transition: 0.8s;
        }
        p {
            opacity: 0.8;
        }
        .Medal {
            position: absolute;
            transition: 0.8s;
            margin-bottom: 80px;
            transform: scale(1.3) translateY(-500px);
        }
    }

    .ellipsis {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }

    .Prize:hover {
        color: #ebc746;

        .Medal {
            transform: scale(1) translateY(0px);
        }
        .Rocket {
            transform: scale(5) translateX(50px) translateY(-50px);
            opacity: 0;
        }
    }

    .Item:hover, .Prize:hover {
        transform: scale(1.025) translateY(-5px);
    }

    .Item {
        cursor: pointer;
        transition: 0.3s;
        width: 100%;
        height: 350px;
        border-radius: 30px;
        overflow: hidden;
        position: relative;
        box-shadow: 0px 0px 15px -8px #ddd;
        border: 0.01px solid rgb(240, 240, 240);


        .background {
            height: 70%;
            width: 100%;
            object-fit: cover;
            filter: brightness(0.8);
            margin-top: -50px;
        }

        .TextBox {
            width: calc(100% - 60px);
            padding: 10px 30px;
            height: 50%;
            position: absolute;
            bottom: 0px;
            background-color: rgb(250, 250, 250);


            label {
                padding: 3px 10px;
                border-radius: 10px;
                background-color: rgba(24, 118, 251, 0.1);
                color: #1876FB;
            }

            .LabelBox {
                display: flex;
                flex-direction: row;
                gap: 13px;
            }

            .logo {
                width: 80px;
                height: 80px;
                margin-top: -60px;
            }

            h1 {
                margin-bottom: 15px;
                font-size: 30px;
                margin-top: 5px;
                word-break: keep-all;
            }

        }
    }

    .TitleSection {
        height: 50vh;
        margin-top: 130px;
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        h1 {
            font-size: 60px;
        }

    }

    .Effect {
        right: 0px;
        display: flex;
        flex-direction: column;
        gap: 20px;

        .TimeLine {
            display: grid;
            grid-template-columns: 20px 1fr 20px;
            flex-direction: row;
            align-items: center;
        }

        .TimeLineArea {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            .Event {
                width: 100%;
                color: rgb(100, 100, 100);
            }

            .Event::first-line {
                font-size: 10px;
            }
        }

        .line {
            width: 100%;
            height: 1px;
            background-color: #26CE8A;
        }


        .Circle {
            background-color: #26CE8A;
            width: 20px;
            height: 20px;
            border-radius: 100%;

            .Year {
                position: relative;
                margin-top: 30px;
                word-break: keep-all;
            }

            .Event {
                position: relative;
                word-break: keep-all;
                white-space: nowrap;
            }
        }
    }
}