@import "styles/responsive.scss";

.Skillpage {
    width: 100vw;
    height: 100vh;
    overflow-y: scroll;
    will-change: transform;

    @include mobile {
        .SkillGrid {
            grid-template-columns: 90px 1fr !important;
            grid-template-rows: 90px 60px !important;
            width: 100% !important;
            height: auto !important;

            .ContentFrame {

                * {
                    margin-left: 100px !important;
                }
                h2 {
                    font-size: 15px !important;
                }
                h4 {
                    font-size: 10px;
                }
            }
        }
        .SkillSection {
            margin-top: 100px !important;
            height: 300px !important;
            display: flex;
            gap: 10px;
            h1 {
                font-size: 30px !important;
            }
        }

        .Typewriter {
            font-size: 20px !important;
            position: relative !important;
            height: 200px;
        }

        .TextFrame {
            grid-column: 1/3;
            text-align: left;
            h2 {
                width: calc(100% - 50px);
                font-size: 20px !important;
            }
            p {
                width: calc(100% - 50px);
                font-size: 15px !important;
            }
        }

    }

    .Typewriter {
        position: absolute;
        right: 0px;
        font-size: 40px;
        color: rgb(100, 100, 100);
        text-align: right;
    }

    .SkillSection {
        position: relative;
        height: 50vh;
        margin-top: 130px;
        width: 85%;
        h1 {
            font-size: 50px;
        }
    }

    .Block {
        width: calc(100% - 15%);
        margin: 0px auto;
        margin-bottom: 150px;
        .SkillGrid {
            width: 70%;
            height: 220px;
            display: grid;
            grid-template-columns: 150px 1fr;
            grid-template-rows: 150px 70px;
            margin-bottom: 20px;

            .TextFrame {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;

                p {
                    color: #8D8D8D;
                }
            }

            .ImgFrame {
                width: 100%;
                height: 100%;
                border-radius: 30px;
                background-color: rgb(245, 245, 245);
                grid-column: 1/2;
                grid-row: 1/2;
                display: flex;
                align-items: center;
                justify-content: center;
                img {
                    width: 60%;
                }
            }

            .ContentFrame {
                grid-row: 1/2;
                grid-column: 1/3;
                width: 100%;
                border-radius: 30px;
                height: 100%;
                background-color: rgb(250, 250, 250);
                display: flex;
                flex-direction: column;
                justify-content: center;

                h4 {
                    color: grey;
                }

                * {
                    margin-left: 180px;
                }
            }
        }

    }
}
