@import "styles/responsive.scss";

.MainPage {
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0px;
    top: 0px;
    will-change: transform;


    .Header {
        width: 85%;
        margin: 0 auto;
        margin-top: 40px;
        font-size: 25px;
        color: #929292;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        @include mobile {
            margin-top: 20px;
            width: 90%;
            font-size: 19px;
        }
    }

    .Text {
        position: absolute;
        bottom: 15%;
        left: 7.5%;

        @include mobile {
            bottom: 60px;
            width: calc(100% - 15%) !important;

            .Logo {
                height: 13vw !important;
            }
            .hello {
                font-size: 1.3rem !important;
            }

            .name {
                font-size: 2.8rem !important;
            }
        }


        .Logo {
            height: 6vw;
        }

        .hello {
            font-size: 2.5vw;
            color: #676767;
        }

        .name {
            font-size: 5.5vw;
        }

    }
}